<template>
  <div class="sdboxbg">
    <!-- <h1>login</h1> -->

    <div class="sdbox">
      <div class="sdheader">
        <div class="sdrow">
          <p>elm</p>
        </div>
      </div>
      <!--  -->
      <div class="sdinput">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="user_name">
            <el-input placeholder="" v-model.trim="ruleForm.user_name"></el-input>
          </el-form-item>
        </el-form>

        <!-- ============================================== -->
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="password">
            <el-input type="password" placeholder="" v-model.trim="ruleForm.password"></el-input>
          </el-form-item>
        </el-form>

        <!--  -->
        <div class="sdann">
          <button type="primary" @click="Login()"></button>
        </div>
        <div class="sdzhushi">
          <p></p>
          <p></p>
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      list: "",
      ruleForm: {
        user_name: "",
        password: ""
      },
      rules: {
        user_name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 18, message: " 3  18 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 20, message: " 3  20 ", trigger: "blur" }
        ]
      }
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    async Login() {
      let { data: res } = await this.$http.post("/admin/login", this.ruleForm);
      console.log(res);
      if (res.status == 1) {
        this.$message({
          message: "",
          type: "success"
        });
        sessionStorage.setItem("token", res.status.token);
        this.$router.push('/home/Shouye');
      }
    }
  },
  mounted() {
    this.Login();
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

