<template>
  <div>
    <div class="sdheaderdf">
      <div class="sdlh">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <div class="sdimg">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#eff2f7"
          text-color="#000"
          active-text-color="#ffd04b"
        >
          <el-submenu index="6">
            <template slot="title">
              <img class="sdimage" src="//elm.cangdu.org/img/default.jpg" alt />
            </template>
            <el-menu-item index="6-1"></el-menu-item>
            <el-menu-item index="6-2" @click="open"></el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>

    <!-- ================================================================ -->
    <div class="sdpadding">
      <el-table :data="tableData" height="800"  style="width: 100%">
        <el-table-column prop="user_name" label=""></el-table-column>
        <el-table-column prop="create_time" label=""></el-table-column>
        <el-table-column prop="city" label=""></el-table-column>
        <el-table-column prop="admin" label=""></el-table-column>
      </el-table>
    </div>
    <div class="block">
      <!-- <span class="demonstration"></span> -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      tableData: [],
      activeIndex: "1",
      queryInfo: {
        pagenum: 1,
        pagesize: 20
      },
      total: 20000
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  /**
   * DOM$ el
   */
  created() {
    this.getuserList();
    //
  },
  // 
  methods: {
    async getuserList() {
      // /admin/all?offset=0&limit=20
      let { data: res } = await this.$http.get(
        `/admin/all?offset=${this.queryInfo.pagenum}&limit=${this.queryInfo.pagesize}`
      );
      this.tableData = res.data
      // this.total=res
      console.log(this.tableData);
    },

    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getuserList();
      // console.log(` ${val} `);
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getuserList();
      // console.log(`: ${val}`);
    },
    open() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/Login");
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},

  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {},
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

