.sdheaderdf {
  width: 100%;
  background-color: #eff2f7;
  display: flex;
  justify-content: space-between;
}
.sdimage {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-right: -30px;
}

.sdheaderdf_p {
  color: #97a8be;
  font-size: 12px;
  line-height: 71px;
  margin-left: 30px;
}
.sdlh {
  padding: 20px;
}
.sdpadding {
  margin: 20px;
  border: 1px solid #ddd;
}
</style>
