<template>
  <div>
    <div class="sdheaderdf">
      <div class="sdlh">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <div class="sdimg">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#eff2f7"
          text-color="#000"
          active-text-color="#ffd04b"
        >
          <el-submenu index="6">
            <template slot="title">
              <img class="sdimage" src="//elm.cangdu.org/img/default.jpg" alt />
            </template>
            <el-menu-item index="6-1"></el-menu-item>
            <el-menu-item index="6-2" @click="open"></el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <!-- ================================================================= -->
    <div class="sdpadding">
      <el-table :data="tableData" style="width: 100%" >
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.address }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ props.row.item_id }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ props.row.restaurant_id }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.description }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.rating }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.rating }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.category }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.month_sales }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column background="#ddd" label="" prop="name"></el-table-column>
        <el-table-column background="#ddd" label="" prop="description"></el-table-column>
        <el-table-column background="#ddd" label="" prop="rating"></el-table-column>
        <el-table-column background="#ddd" label="" prop width="250">
          <el-row>
            <el-button size="mini"></el-button>
            <el-button size="mini"></el-button>
            <el-button type="danger" size="mini"></el-button>
          </el-row>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 5, 10, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      activeIndex: "1",
      tableData: [],
      queryInfo: {
        // query: "",
        pagenum: 1,
        pagesize: 20
      },
      total: 20000
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  /**
   * DOM$ el
   */
  created() {
    this.getshopping();
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getshopping();
      // console.log(` ${val} `);
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getshopping();
      // console.log(`: ${val}`);
    },

    //   async getuserList() {
    //   // `/v1/users/list?offset=${this.userPage.page}&limit=${this.userPage.pageSize}
    //   let { data: res } = await this.$http.get(`/v1/users/list?offset=${this.queryInfo.pagenum}&limit=${this.queryInfo.pagesize}`);
    //   this.tableData = res; // this.total=res
    //   console.log(this.tableData);
    // },
    async getshopping() {
      //   /shopping/v2/foods?offset=0&limit=20&restaurant_id=undefined
      let { data: res } = await this.$http.get(
        `/shopping/v2/foods?latitude=39.90469&longitude=116.407173&offset=${this.queryInfo.pagenum}&limit=${this.queryInfo.pagesize}`
      );
      this.tableData = res;
      console.log(res);
    },
    open() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/Login");
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},

  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {},
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

