<template>
  <div>
    <div class="sdheaderdf">
      <div>
        <p class="sdheaderdf_p">
          <router-link tag="p" to="/home/shouye"></router-link>
        </p>
      </div>
      <div class="sdimg">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#eff2f7"
          text-color="#000"
          active-text-color="#ffd04b"
        >
          <el-submenu index="6">
            <template slot="title">
              <!-- <img src="@/assets/logo.png" alt class="sdimg" /> -->
              <img class="sdimage" src="//elm.cangdu.org/img/default.jpg" alt />
            </template>
            <el-menu-item index="6-1"></el-menu-item>
            <el-menu-item index="6-2" @click="open"></el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="sdheader">
      <p></p>
      <div class="sdoang">
        <ul>
          <li class="sdO">:</li>
          <li class="sdddd">
            <span>9</span>
          </li>
          <li class="sdddd">
            <span>1</span>
          </li>
          <li class="sdddd">
            <span>31</span>
          </li>
        </ul>
        <ul>
          <li class="sd1">:</li>
          <li class="sdddd">
            <span>52521</span>
          </li>
          <li class="sdddd">
            <span>19648</span>
          </li>
          <li class="sdddd">
            <span>81503</span>
          </li>
        </ul>
      </div>
    </div>
    <!-- ========================================================= -->
    <!-- <el-card class="box-card"> -->
    <div class="text item">
      <div id="echart" style="height:400px;"></div>
    </div>
    <!-- </el-card> -->
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
       activeIndex: "1"
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
     handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
     open() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/Login");
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    setEchart() {
      let myechart = this.$echarts.init(document.getElementById("echart"));
      let option = {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["", "", ""]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [
            "2020-10-30",
            "2020-10-31",
            "2020-11-01",
            "2020-11-02",
            "2020-11-03",
            "2020-11-04",
            "2020-11-05"
          ]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "",
            type: "line",
            stack: "",
            data: [21, 13, 10, 15, 57, 26, 9]
          },
          {
            name: "",
            type: "line",
            stack: "",
            data: [21, 3, 9, 7, 12, 15, 1]
          },
          {
            name: "",
            type: "line",
            stack: "",
            data: [50, 27, 23, 45, 68, 62, 31]
          }
        ]
      };

      myechart.setOption(option);
    }
  },
  //  
  /**
   * data
   */
  beforeCreate() {},
  /**
   * DOM$ el
   */
  created() {},
  /**
   *  render 
   */
  beforeMount() {},
  /**
   * el  vm.$ el 
   *  root  mounted  vm.$ el 
   */
  mounted() {
    this.setEchart();
  },
  /**
   *  DOM 
   * 
   */
  beforeUpdate() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {},
  /**
   * 
   */
  beforeDestroy() {},
  /**
   * Vue Vue 
   * 
   */
  destroyed() {}
};
</script> 

